import { useState } from 'react';
import { Notify } from 'notiflix/build/notiflix-notify-aio';
import {
  BoxForm,
  Form,
  Label,
  Input,
  Button,
  Select,
  Option,
} from './CreateUser.styled';
import { createUser } from 'components/API/API';

export const CreateUser = ({ setIsCreate }) => {
  const [name, setName] = useState('');
  const [password, setPassword] = useState('');
  const [role, setRole] = useState('user');

  const createUserFn = async e => {
    e.preventDefault();
    if (!name.trim() || !password.trim() || !role.trim())
      return Notify.warning('  ');
    try {
      await createUser(name, password, role);
      setIsCreate(false);
      window.location.reload();
      Notify.success(` `);
    } catch (error) {
      setIsCreate(false);
      Notify.failure('        ');
    }
  };

  return (
    <BoxForm>
      <Form onSubmit={createUserFn}>
        <Label>
          ':
          <Input
            type="text"
            value={name}
            onChange={e => setName(e.target.value)}
          />
        </Label>
        <Label>
          :
          <Select onChange={e => setRole(e.target.value)} value={role}>
            <Option value="user">user</Option>
            <Option value="moderator">moderator</Option>
          </Select>
        </Label>
        <Label>
          :
          <Input
            type="password"
            value={password}
            onChange={e => setPassword(e.target.value)}
          />
        </Label>
        <Button type="submit"></Button>
      </Form>
    </BoxForm>
  );
};
